/* ProjectPage.css */

.project-page {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
    background-color: #f9f9f9;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  }
  
  .project-page h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #2c3e50;
    border-bottom: 2px solid #ccc;
    padding-bottom: 0.5rem;
  }
  
  .project-page img {
    width: 100%;
    height: auto;
    margin: 1rem 0;
    border-radius: 8px;
  }
  
  .project-page h2 {
    font-size: 1.5rem;
    margin-top: 1.5rem;
    color: #34495e;
  }
  
  .project-page p {
    line-height: 1.6;
    margin: 1rem 0;
  }
  
  .project-page ul {
    list-style: disc;
    margin-left: 1.5rem;
  }
  
  .project-page li {
    margin: 0.5rem 0;
  }
  